.side-circles {
	position: relative;
	z-index: 1;
	overflow: hidden;
	&:before {
		content: '';
		position: absolute;
		top: -535px;
		left: -520px;
		width: 875px;
		height: 875px;
		border: 40px solid rgba($white, 0.08);
		border-radius: 50%;
	}
	&:after {
		content: '';
		position: absolute;
		top: -110px;
		right: -180px;
		width: 420px;
		height: 420px;
		border: 21px solid rgba($white, 0.08);
		border-radius: 50%;
	}
}

.top-triangle {
	position: relative;
	z-index: 1;
	&:before {
		content: '';
		position: absolute;
		top: -12px;
		left: 50%;
		transform: translateX(-50%) rotate(45deg);
		width: 24px;
		height: 24px;
		background: $white;
	}
}

.overlay__grad {
	position: relative;
	z-index: 1;
	overflow: hidden;
	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: linear-gradient(to right, var(--brand-primary), var(--secondary));
		opacity: 0.9;
		z-index: -1;
	}
}

.overlay__gradient {
	position: relative;
	z-index: 1;
	overflow: hidden;
	.overlay_after {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: linear-gradient(to right, var(--brand-primary), var(--secondary));
		opacity: 0.94;
		z-index: -1;
		margin: 0;
	}
	&-left .overlay_after {
		background: linear-gradient(10deg, var(--brand-primary), var(--secondary));
	}
}

.overlay__white {
	position: relative;
	z-index: 1;
	overflow: hidden;
	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: $white;
		opacity: 0.94;
		z-index: -1;
	}
}

.overlay__dark {
	position: relative;
	z-index: 1;
	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: $black;
		opacity: 0.75;
		z-index: -1;
	}
}

.overlay__purple {
	position: relative;
	z-index: 1;
	> .after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: linear-gradient(to right, var(--brk-secondary-4) 20%, var(--secondary) 70%);
		opacity: 0.94;
		z-index: -1;
	}
}

.overlay__dot {
	position: relative;
	z-index: 1;
	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: rgba(#172844, 0.7);
		background-image: radial-gradient(#101C2F 24%, transparent 25%);
		background-size: 3px 3px;
		opacity: 0.94;
		z-index: -1;
	}
}

.overlay__curve {
	position: relative;
	z-index: 1;
	overflow: visible;
	&:after {
		content: '';
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
