		}
		&_bottom-right{
			background-position: right bottom;
		}
		&_left-center{
			background-position: left center;
		}
		&_right-center{
			background-position: right center;
		}
		&_center{
			background-position: center center;
		}
	}
}

/* hexagon */
.hexagon {
	display: flex;
	justify-content: center;
	align-items: center;
	// width: 290px;
	height: 320px;
	margin: 0 auto;
	background: #fff;
	padding: 30px 30px;
	clip-path: polygon(80% 0, 100% 50%, 80% 100%, 20% 100%, 0 50%, 20% 0);
	position: relative;
	z-index: 1;
	@media(min-width: 480px) {
		width: 430px;
		height: 380px;
		padding: 30px 70px;
		clip-path: polygon(75% 0, 100% 50%, 75% 100%, 25% 100%, 0 50%, 25% 0);
	}
	@media(min-width: 1200px) {
		margin: 0 auto;
	}
	&__wrap {
		@media(min-width: 992px) {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
		@media(min-width: 1200px) {
			margin: -180px auto;
			.hexagon {
				margin: 0;
			}
		}
	}
	> .after {
		content: '';
		position: absolute;
		top: -2px;
		left: -2px;
    width: calc(100% + 4px);
    height: calc(100% + 4px);
		background: var(--brand-primary);
		background: linear-gradient(to left, var(--brand-primary), var(--secondary));
		clip-path: polygon(80% 0, 100% 50%, 80% 100%, 20% 100%, 0 50%, 20% 0);
		opacity: 0;
		transform: scale(1);
		will-change: transform;
		transition: all .4s ease;
		z-index: -1;
		@media(min-width: 480px) {
			clip-path: polygon(75% 0, 100% 50%, 75% 100%, 25% 100%, 0 50%, 25% 0);
		}
	}
	.highlight-massive > .after {
		height: 3px;
		top: 98%;
	}
	.btn {
		background: transparent;
		color: $white;
		opacity: 0;
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		margin-right: 0;
		margin-left: 0;
		> .before, > .after {
			background: $white;
		}
		.border-btn {
			border-color: $white;
		}
		&:hover {
			color: $gray-dark !important;
		}
	}
	.cover {
		position: relative;
		transition: all .4s ease;
	}
}

