/* z-index */
@for $i from 0 through 5 {
	.brk-z-index-#{$i*10} {
		z-index: #{$i*10};
	}
}

@for $i from 1 through 5 {
	.brk-z-index--#{$i} {
		z-index: -#{$i};
	}
}
/* z-index */

/* brk-before/after-element */
.brk-before-element,
.brk-after-element {
	position: absolute;
	left: 0;
	right: 0;

	svg {
		max-width: 100%;
	}

	&-svg {
		position: absolute;
		left: 0;
		right: 0;
		max-width: 100%;
	}

	&_bevel-right {
		overflow: hidden;
		height: 80px;
		background: #fff;

		@media (max-width: 991px) {
			display: none;
		}
	}
}
.brk-before-element {
	top: 0;

	svg {
		margin-top: -1px;
	}

	&-svg {
		top: -1px;
	}

	&_bevel-right {
		-webkit-clip-path: polygon(0 0, 100% 100%, 100% 0);
		clip-path: polygon(0 0, 100% 100%, 100% 0);
	}
}
.brk-after-element {
	bottom: 0;

	svg {
		margin-bottom: -1px;
	}

	&-svg {
		bottom: -1px;
	}

	&_bevel-right {
		-webkit-clip-path: polygon(0 100%, 100% 100%, 100% 0);
		clip-path: polygon(0 100%, 100% 100%, 100% 0);
	}
}
/* brk-before/after-element */

/* brk-layer */
.brk-layer {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 1;
}
